import React, {Component} from 'react';
import PropTypes from 'prop-types'
import {Link } from 'react-router-dom';
import isEmpty from '../../validation/is-empty';

class ProfileItem extends Component{
    render(){

        const {profile} = this.props;

        // console.log(profile, '<--------;this.props.profile {} , ProfileItem.js');
        console.log({profile}.profile.user.name, '<--&&&&&&------;frm profile.user.name ProfileItem.js');

        return(
            <div className='card card-body bg-light mb-3'>
                <div className='row'>
                    {/*NOTE: remember on the back end, we used populate: allows us to extract user name for avatar from 'user'*/}
                    <div className='col-2'>
                        <img src={profile.user.avatar} alt='#' className='rounded-circle'/>
                    </div>

                    <div className='col-lg-6 col-md-4 col-8'>
                        <h3>{profile.user.name}</h3>
                        {/*if they have a company, display company along with status*/}
                        <p>
                            {profile.status} {isEmpty(profile.company) ? null: (<span>at {profile.company}</span>)}
                        </p>

                        <p>
                            {isEmpty(profile.location) ? null : (<span>{profile.location}</span>)}
                        </p>

                        <Link to={`/profile/${profile.handle}`} className='btn btn-info'>
                            View Profile
                        </Link>

                    </div>
                    {/*if below a medium size, display none, if above medium size screen display as block */}
                    <div className='col-md-4 d-none d-md-block'>
                        <h4>Skill Set</h4>
                        <ul className='list-group'>

                            {/*map through the skills [], map the first 4 */}
                            {profile.skills.slice(0,4).map(
                                (skill, index)=>
                                    (<li key={index} className='list-group-item'>
                                        <i className='fa fa-check pr-1'/>
                                        {skill}
                                    </li>)
                            )}
                        </ul>
                    </div>



                </div>

            </div>
        )
    }
}



ProfileItem.propTypes={
    profile: PropTypes.object.isRequired
};

export default ProfileItem




