import React, {Component} from 'react';
// import PropTypes from 'prop-types';
import Moment from 'react-moment';

class ProfileCreds extends Component{
    render(){

        const {experience, education} = this.props;

        const expItems = experience.map(exp=>(
            <li key={exp._id} className='list-group-item'>

                <h4>{exp.company}</h4>

                <p>
                    <Moment format ="MM/DD/YYYY">
                        {exp.from}
                    </Moment> - {exp.to === null ? (' Now') : (<Moment format='MM/DD/YYYY'> {exp.to}</Moment>)}
                </p>

                <p>
                    <strong> Posistion:</strong>
                    {exp.title}
                </p>

                <p>
                    {exp.location === '' ? null : (<span><strong>Location: {exp.location}</strong></span>)}
                </p>

                <p>
                    {exp.description === '' ? null : (<span><strong>Description: {exp.description}</strong></span>)}
                </p>
            </li>
        ));


        const eduItems = education.map(edu=>(
            <li key={edu._id} className='list-group-item'>

                <h4>{edu.school}</h4>

                <p>
                    <Moment format ="MM/DD/YYYY">
                        {edu.from}
                    </Moment> - {edu.to === null ? (' Now') : (<Moment format='MM/DD/YYYY'> {edu.to}</Moment>)}
                </p>

                <p><strong> Degree:</strong>
                    {edu.degree}
                </p>

                <p><strong> Field of Study:</strong>
                    {edu.fieldofstudy}
                </p>

                <p>
                    {edu.description === '' ? null : (<span><strong>Description: {edu.description}</strong></span>)}
                </p>
            </li>
        ));

        return(
            <div className='row'>
                <div className='col-md-6'>
                    <h3 className='text-center text-info'>Experience</h3>

                    {/*TEST && Check if there are any experience items present*/}

                    {expItems.length > 0 ? (<ul className='list-group'>{expItems}</ul>

                    ): <p className='text-center'> No Experience listed</p>}

                </div>

                <div className='col-md-6'>
                    <h3 className='text-center text-info'>Education</h3>

                    {/*TEST && Check if there are any experience items present*/}

                    {eduItems.length > 0 ? (<ul className='list-group'>{eduItems}</ul>

                    ): <p className='text-center'> No Education listed</p>}

                </div>


            </div>
        )
    }
}


ProfileCreds.propTypes={
    // profile: PropTypes.object.isRequired
};

export default ProfileCreds


