import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {Link} from 'react-router-dom';

class ProfileGitHub extends Component{

    constructor(props){
        super(props);
        this.state={
            clientId: '1d9dd9d6b7e06e46f5d7',
            clientSecret:'63accfea94d8b71c236153983fdd7dcbf0c09f11',
            count: 5,
            sort: 'created: asc',
            repos: []
        }
    };

    componentDidMount(){
        const {username} = this.props;
        const {count, sort, clientId, clientSecret} = this.state;


        fetch(`https://api.github.com/users/${username}/repos?per_page=${count}&sort=${sort}&client_id=${clientId}&client_secret=${clientSecret}`)
            .then(res=>res.json())
            .then(data =>{

                if(this.refs.myRef){
                    this.setState({repos: data})  // the whole point of this api is to fetch the repos hence, we change the state in the promise
                }

            })
            .catch(err=> console.log(err))
    }
    //NOTE: with fetch(), the 1st .then() has to be a json res

    render(){

        const {repos} = this.state;

        //make a mapper from the api resp

        const repoItems = repos.map(repo=>(
            <div key={repo.id} className='card card-body mb-2'>
                <div className='row'>
                    <div className='col-md-6'>
                        <h4>
                            <Link to={repo.html_url} className='text-info' target="_blank">
                                {repo.name}
                            </Link>
                        </h4>

                        <p>
                            {repo.description}
                        </p>

                    </div>

                    <div className='col-md-6'>
                        <span className='badge badge-info mr-1'>
                            Stars: {repo.stargazers_count}
                        </span>

                        <span className='badge badge-secondary mr-1'>
                            Stars: {repo.watchers_count}
                        </span>

                        <span className='badge badge-success'>
                            Stars: {repo.forks_count}
                        </span>
                    </div>


                </div>
            </div>
        ));

        return(
            <div ref='myRef'>
                <hr />
                <h3 className='mb-4'>Latest Github Repos</h3>
                {repoItems}
            </div>
        )
    }
}


ProfileGitHub.propTypes={
    username: PropTypes.string.isRequired
};

export default ProfileGitHub


